import { Component } from "@angular/core";
import { IBreadcrumb } from 'src/app/shared/breadcrumb/breadcrumb.component';
import { partners } from "./partners.mock";

@Component({
  selector: 'movida-partners',
  templateUrl: './movida-partners.component.html',
  styleUrls: ['./movida-partners.component.scss']
})
export class MovidaPartnersComponent {

  public partners = partners
  public partnersOrigin = partners
  public clipboardCopy = false
  public buttonClose = false
  public textSearch = ''
  public breadCrumbs: IBreadcrumb[] = [
    {
      id: 'breadCrumbLinkParceiros',
      label: 'Parceiros',
      link: '/parceiros'
    }
  ];

  searchPartners() {
    this.partners = this.partnersOrigin.filter(p=>{
      return p.name.toLowerCase().includes(this.textSearch.toLowerCase()) || 
             p.cnpj.replace(".", "").includes(this.textSearch.replace(".", ""))
    })
  }

  copyToClipboard(text) {
    navigator.clipboard.writeText(text);

    setTimeout(()=>{
      this.clipboardCopy = false;
    }, 2000);
    this.clipboardCopy = true;
  }
  clearSearchPartners() {
    this.textSearch = ''
    this.partners = this.partnersOrigin
  }

}
